import { supabase } from 'index'
import { nameList } from 'nameList'
import React, { useEffect, useState } from 'react'
import Swal from 'sweetalert2'

const App = () => {
	const [commentList, setCommentList] = useState([])
	const [comment, setComment] = useState('')

	useEffect(() => {
		const A = async () => {
			const { data } = await supabase.from('lheesung').select()
			setCommentList(data as any)
		}
		A()
	}, [])

	const handleClick = () => {
		Swal.fire({
			title: '  ?',
			showDenyButton: true,
			confirmButtonText: '',
			denyButtonText: ``,
		}).then(() => {
			Swal.fire({
				title: '?',
				showDenyButton: true,
				confirmButtonText: '',
				denyButtonText: ``,
			}).then(() => {
				Swal.fire({
					title: '??',
					showDenyButton: true,
					confirmButtonText: '',
					denyButtonText: ``,
				}).then(() => {
					Swal.fire({
						title: '??????',
						showDenyButton: true,
						confirmButtonText: '',
						denyButtonText: ``,
					}).then(() => {
						Swal.fire({
							title: '   ?',
							showDenyButton: true,
							confirmButtonText: '',
							denyButtonText: ``,
						}).then(() => {
							Swal.fire({
								title: '    ',
								showDenyButton: true,
								confirmButtonText: '1117',
								denyButtonText: `13 -5`,
							}).then(() => {
								Swal.fire({
									title: '    ',
									showDenyButton: true,
									confirmButtonText: '',
									denyButtonText: ``,
								}).then(() => {
									Swal.fire({
										title: '   ',
										showDenyButton: true,
										confirmButtonText: '',
										denyButtonText: ``,
									}).then(() => {
										Swal.fire({
											title: '?',
											showDenyButton: true,
											confirmButtonText: '',
											denyButtonText: ``,
										}).then(() => {
											Swal.fire({
												title: ' ',
												showDenyButton: true,
												confirmButtonText: '',
												denyButtonText: ``,
											}).then(() => {
												Swal.fire({
													title: '  ?',
													showDenyButton: true,
													confirmButtonText: ' ',
													denyButtonText: ` `,
												}).then(async () => {
													const { error } = await supabase
														.from('lheesung')
														.upsert({ id: commentList.length + 1, name: nameList[(commentList.length + 1) % 40], content: comment })
													if (!error) {
														Swal.fire('!')
														window.location.reload()
													} else {
														Swal.fire('!')
													}
												})
											})
										})
									})
								})
							})
						})
					})
				})
			})
		})
	}
	return (
		<div className="background w-full mt-32 flex flex-col items-center justify-center gap-20">
			<span className="text-white text-3xl font-bold title"> </span>
			<div className="flex flex-wrap">
				{Array.from({ length: 50 }).map((_, i) => (
					<img key={i} className="img w-20" src="https://velog.velcdn.com/images/ubin_ing/post/265df123-5ced-4596-b761-5d67d65d6ad8/image.jpeg" />
				))}
			</div>
			<span className=" text-6xl text-white">    ~~~</span>
			<div className="flex flex-col gap-2">
				<textarea
					onChange={(e) => setComment(e.target.value)}
					value={comment}
					className=" p-4 border-blue-700 border-4 border-solid w-[600px] h-[200px] resize-none text-xl"
					placeholder="  "
				/>
				<button onClick={handleClick} className=" bg-blue-500 border-red-500 border-4 border-solid rounded-md px-5 py-3 text-white">
					 
				</button>
			</div>
			<span className=" text-6xl text-white">    </span>
			<div className="flex flex-col gap-4 pb-20">
				{commentList.map((commentItem: any) => (
					<div key={commentItem.id} className="flex w-[68vw] px-4 py-2 bg-white flex-col">
						<div className="flex items-center gap-4">
							<img
								className="w-12 h-12 rounded-full"
								src="https://velog.velcdn.com/images/ubin_ing/post/2185340c-088c-4287-b211-b7f1443f4cd9/image.jpeg"
							/>
							<span className="text-lg text-gray-500">{commentItem.name}</span>
						</div>
						<span className="text-3xl text-black">{commentItem.content}</span>
					</div>
				))}
			</div>
		</div>
	)
}

export default App
